import { g2Factory, getConsoleConfig, Utils } from '@alicloud/console-shared-utils';

var COMPONENT_NAME = 'ConsoleBoxChart';
var cfg = {
    // 初始化前对props的预处理函数
    getUnifiedProps: function (props) {
        var config = props.config;
        var defaultConfig = getConsoleConfig()[COMPONENT_NAME];
        var newConfig = Utils.merge({}, defaultConfig, config);
        // TODO 处理padding
        return Object.assign({}, props, {
            padding: props.padding || newConfig.padding || 'auto',
            config: newConfig,
        });
    },
    drawChart: function (chart, config, data) {
        var dv = data[0].data || [];
        var res = dv.map(function (obj) {
            obj.range = [obj.low, obj.q1, obj.median, obj.q3, obj.high];
            return obj;
        });
        chart.source(res, {
            range: {
                max: 35
            }
        });
        chart.tooltip({
            showTitle: false,
            crosshairs: {
                type: 'rect'
            },
            itemTpl: '<li data-index={index} style="margin-bottom:4px;">'
                + '<span style="background-color:{color};" class="g2-tooltip-marker"></span>'
                + '{name}<br/>'
                + '<span style="padding-left: 16px">最大值：{high}</span><br/>'
                + '<span style="padding-left: 16px">上四分位数：{q3}</span><br/>'
                + '<span style="padding-left: 16px">中位数：{median}</span><br/>'
                + '<span style="padding-left: 16px">下四分位数：{q1}</span><br/>'
                + '<span style="padding-left: 16px">最小值：{low}</span><br/>'
                + '</li>'
        });
        chart.schema().position('x*range')
            .shape('box')
            .tooltip('x*low*q1*median*q3*high', function (x, low, q1, median, q3, high) {
            return {
                name: x,
                low: low,
                q1: q1,
                median: median,
                q3: q3,
                high: high
            };
        })
            .style({
            stroke: '#545454',
            fill: '#1890FF',
            fillOpacity: 0.3
        });
        chart.render();
    },
};
var ConsoleBoxChart = g2Factory(COMPONENT_NAME, cfg);

export default ConsoleBoxChart;
